@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: "VT323", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#editor-container, #preview-container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

#editor-container textarea, #preview-container #preview {
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
  flex-grow: 1;
  height: 50vh;
  border: 2px solid #605d74;
  border-radius: 5px;
  overflow-wrap: break-word;
}

.outer-container {
  background: url("http://api.thumbr.it/whitenoise-361x370.png?background=d7f7f3ff&noise=cccccc&density=25&opacity=10");
  display: flex;
  height: 100vh;
  flex-direction: column;
}

.container {
  width: 95%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  margin: 16px auto;
}

#title {
  margin: 16px auto;
  font-size: 1.4em;
  color: #605d74;
}

#editor-container textarea {
  display: block;
  overflow-y: auto;
  resize: none;
}

#preview-container #preview {
  background-color: white;
  overflow-y: auto;
}

#preview-container pre {
  overflow-x: auto;
}

.group-toggle {
  display: flex;
}

.group-toggle h2 {
  font-size: 1.3em;
  color: #605d74;
}

.group-toggle h2 + div {
  flex-grow: 1;
  -ms-grid-row-align: center;
  align-self: center;
}

.group-toggle h2 + div > button {
  border: 1px solid #605d74;
  background-color: transparent;
  padding: 4px;
  margin-left: 16px;
}

.group-toggle h2 + div > button:hover,
.group-toggle h2 + div > button:active {
  background-color: #dddddf;
}

.group-toggle h2 + div img {
  width: 1em;
}

#btn-toggle {
  display: inline-block;
  margin-left: 16px;
  -ms-grid-row-align: center;
  align-self: center;
}

#btn-toggle-pr {
  border: 1px solid #605d74;
  padding: 8px;
  background-color: transparent;
}

#btn-toggle-pr:hover, #btn-toggle-pr:active {
  background-color: #dddddf;
}

@media (min-width: 720px) {
  #editor-container, #preview-container {
    flex-grow: unset;
  }
  .container {
    flex-direction: row;
  }
  #editor-container,
  #preview-container {
    width: 49%;
  }
  #editor-container {
    justify-self: flex-start;
  }
  #btn-toggle {
    display: none;
  }
  #preview-container {
    justify-self: flex-end;
  }
}
